:root {
  --color: #3d4e61;
  --gradient: linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
}

@font-face {
  font-family: Avenir;
  src: url("Avenir-Book.657c3568.ttf");
  font-weight: 300;
}

@font-face {
  font-family: Avenir;
  src: url("Avenir-Heavy.01d9ab74.ttf");
  font-weight: 800;
}

body {
  margin: 0;
  padding: 0 8px;
  font-family: Avenir, sans-serif;
}

.form {
  margin-top: 20px;
}

.form__input {
  box-sizing: border-box;
  color: var(--color);
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  width: 100%;
  font-family: inherit;
  box-shadow: 0 1px 8px #3d4e611a;
}

.large {
  background: url("Search.ccd3c07b.svg") 26px 25px / 19px no-repeat;
  height: 70px;
  padding-left: 62px;
  font-size: 16px;
}

.small {
  background: url("Search.ccd3c07b.svg") 12px / 11px no-repeat;
  height: 42px;
  margin-top: 20px;
  padding-left: 33px;
  font-size: 14px;
}

.large:hover {
  background-image: url("Search.ccd3c07b.svg");
  background-repeat: no-repeat;
  background-color: var(--gradient);
  box-shadow: 0 4px 4px #00000040;
}

.small:hover {
  background-image: url("Search.ccd3c07b.svg");
  background-repeat: no-repeat;
  box-shadow: 0 3px 8px #3d4e6133;
}

.form__input::placeholder {
  color: var(--color);
  font-family: inherit;
  font-weight: 300;
}

.small::placeholder {
  font-size: 14px;
}

.form__input:focus {
  border-radius: 3px;
  outline: none;
  font-weight: 800;
}

.large:focus {
  background-image: url("Search.ccd3c07b.svg");
  background-color: var(--gradient);
}

.small:focus {
  color: var(--color);
  background-image: url("Search.ccd3c07b.svg");
  background-color: var(--gradient);
  background-size: 11px;
}

